"use client";

import { useState, useEffect } from 'react';
import { Phone, Search,} from 'lucide-react';

// You can move this to its own file like `src/components/Header.tsx`
const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  // const [isMenuOpen, setIsMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 10);
    };
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  // const navLinks = ["Home", "About Us", "Aquarium", "Features", "Gallery", "Blog", "Contact"];

  return (
    <header className={`fixed top-0 left-0 w-full z-50 transition-colors duration-300 ${isScrolled ? 'bg-[#0D1B2A]' : 'bg-transparent'}`}>
      <div className="container mx-auto px-6 py-4 flex justify-between items-center">
        {/* Logo */}
        <div className="flex items-center space-x-2">
          {/* <Seahorse className="h-8 w-8 text-cyan-400" /> */}
          <span className="text-white text-2xl font-bold">AQUAPRO</span>
        </div>

        {/* Desktop Navigation */}
        {/* <nav className="hidden md:flex items-center">
          <ul className="flex items-center space-x-6">
            {navLinks.map((link) => (
              <li key={link}>
                <a href="#" className={`text-white hover:text-cyan-400 transition-colors ${link === "Home" ? 'text-cyan-400' : ''}`}>
                  {link}
                </a>
              </li>
            ))}
          </ul>
        </nav> */}

        {/* Right side icons - Desktop */}
        <div className="hidden md:flex items-center space-x-6">
          <div className="flex items-center space-x-2">
            <Phone className="h-5 w-5 text-cyan-400" />
            <span className="text-white">(800) 123 4567</span>
          </div>
          <Search className="h-6 w-6 text-white hover:text-cyan-400 cursor-pointer" />
        </div>
        
        {/* Mobile Menu Button */}
        {/* <div className="md:hidden">
          <button onClick={() => setIsMenuOpen(!isMenuOpen)} className="text-white">
            {isMenuOpen ? <X size={28} /> : <Menu size={28} />}
          </button>
        </div>*/}
      </div> 

      {/* Mobile Menu */}
      {/* {isMenuOpen && (
        <div className="md:hidden bg-[#0D1B2A] py-4">
          <ul className="flex flex-col items-center space-y-4">
            {navLinks.map((link) => (
              <li key={link}>
                <a href="#" className={`text-white hover:text-cyan-400 transition-colors ${link === "Home" ? 'text-cyan-400' : ''}`}>
                  {link}
                </a>
              </li>
            ))}
             <li className="flex items-center space-x-2 pt-4">
                <Phone className="h-5 w-5 text-cyan-400" />
                <span className="text-white">(800) 123 4567</span>
             </li>
             <li>
                <Search className="h-6 w-6 text-white hover:text-cyan-400 cursor-pointer" />
             </li>
          </ul>
        </div>
      )} */}
    </header>
  );
};


const Page = () => {
  return (
    <>
      <Header />
      <main>
        {/* Hero Section */}
        <section className="h-screen w-full hero-bg flex items-center justify-center">
          <div className="text-center text-white max-w-4xl mx-auto px-4">
            <h1 className="text-5xl md:text-7xl font-bold leading-tight">
              Aquarium Installation and Maintenance
            </h1>
            <p className="mt-4 text-xl md:text-2xl text-gray-200">
              Fastest and easiest way to cycle your new aquarium.
            </p>
            <a
              href="#"
              className="mt-8 inline-block bg-cyan-500 text-white font-bold py-4 px-10 rounded hover:bg-cyan-600 transition-colors duration-300"
            >
              CONTACT US TODAY!
            </a>
          </div>
        </section>

        {/* You can add other sections here */}
        <section className="py-20 bg-[#0c0c0c]">
           <div className="container mx-auto text-center">
              <h2 className="text-4xl text-white">Other Content Goes Here</h2>
           </div>
        </section>

      </main>
    </>
  );
};

export default Page;